<template>
    <div class="m-fb m-fb-index">
        <div class="m-archive-list m-fb-list" v-if="data.length">
            <ul class="u-list">
                <li class="u-item" v-for="(item, i) in data" :key="i">
                    <a class="u-banner" :href="item.post.ID | postLink"
                        ><img :src="showBanner(item.post.post_banner)"
                    /></a>

                    <h2 class="u-post" :class="{ isSticky: item.post.sticky }">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 12 16" width="12" height="16" aria-hidden="true" fill="#555" v-bind:class="'u-icon'" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" d="M4 9H3V8h1v1zm0-3H3v1h1V6zm0-2H3v1h1V4zm0-2H3v1h1V2zm8-1v12c0 .55-.45 1-1 1H6v2l-1.5-1.5L3 16v-2H1c-.55 0-1-.45-1-1V1c0-.55.45-1 1-1h10c.55 0 1 .45 1 1zm-1 10H1v2h2v-1h3v1h5v-2zm0-10H2v9h9V1z"/></svg>
                        <a
                            class="u-title"
                            :style="item.post.color | isHighlight"
                            :href="item.post.ID | postLink"
                            target="_blank"
                            >{{ item.post.post_title }}</a
                        >
                        <span
                            class="u-marks"
                            v-if="item.post.mark && item.post.mark.length"
                        >
                            <i
                                v-for="mark in item.post.mark"
                                class="u-mark"
                                :key="mark"
                                >{{ mark | showMark }}</i
                            >
                        </span>
                    </h2>

                    <div class="u-content">
                        <div
                            class="u-metalist u-boss-list"
                            v-if="
                                item.post.post_meta &&
                                    item.post.post_meta.fb_boss
                            "
                        >
                            <strong>首领</strong>
                            <em>
                                <b
                                    v-for="(c, i) in format(
                                        item.post,
                                        'post_meta.fb_boss'
                                    )"
                                    :key="i"
                                >
                                    {{ c }}
                                </b>
                            </em>
                        </div>

                        <div
                            class="u-metalist u-mode-list c-jx3fb-mode"
                            v-if="
                                item.post.post_meta &&
                                    item.post.post_meta.fb_level
                            "
                        >
                            <strong>模式</strong>
                            <em>{{
                                format(item.post, "post_meta.fb_level") + ""
                            }}</em>
                        </div>
                    </div>

                    <div class="u-misc">
                        <img
                            class="u-author-avatar"
                            :src="item.author.avatar | showAvatar"
                            :alt="item.author.name"
                        />
                        <a
                            class="u-author-name"
                            :href="item.author.uid | authorLink"
                            target="_blank"
                            >{{ item.author.name }}</a
                        >
                        <span class="u-date">
                            Updated on
                            <time>{{
                                item.post.post_modified | dateFormat
                            }}</time>
                        </span>
                    </div>
                </li>
            </ul>
        </div>
        <el-alert
            v-else
            class="m-archive-null"
            title="没有找到相关条目"
            type="info"
            center
            show-icon
        >
        </el-alert>
        <el-button
            class="m-archive-more"
            :class="{ show: hasNextPage }"
            type="primary"
            :loading="loading"
            @click="appendPage(++page)"
            >加载更多</el-button
        >
        <el-pagination
            class="m-archive-pages"
            background
            :hide-on-single-page="true"
            @current-change="changePage"
            :current-page.sync="page"
            layout="total, prev, pager, next, jumper"
            :total="total"
        >
        </el-pagination>
    </div>
</template>

<script>
import lodash from "lodash";
import { getPosts } from "../service/getPost";
import dateFormat from "../utils/dateFormat";
import { __ossMirror } from "@jx3box/jx3box-common/js/jx3box";
import {
    showAvatar,
    authorLink,
    showMinibanner,
} from "@jx3box/jx3box-common/js/utils";
export default {
    name: "Index",
    props: [],
    data: function() {
        return {
            data: [],
            total: 0,
            page: 1,
            pages: 1,
            loading: true,
            subtype: "",
        };
    },
    computed: {
        hasNextPage: function() {
            return this.total > 1 && this.page < this.pages;
        },
        defaultBanner: function() {
            return (
                __ossMirror +
                this.$store.state.map[this.$store.state.zlp]["dungeon"][
                    this.$store.state.fb
                ]["icon"]
            );
        },
    },
    methods: {
        changePage: function(i) {
            this.loading = true;
            getPosts({
                page: i,
                subtype: this.subtype,
            })
                .then((res) => {
                    window.scrollTo(0, 0);
                    this.data = res.data.data.list;
                    this.total = res.data.data.total;
                    this.pages = res.data.data.pages;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        appendPage: function(i) {
            this.loading = true;
            getPosts({
                page: i,
                subtype: this.subtype,
            })
                .then((res) => {
                    this.data = this.data.concat(res.data.data.list);
                    this.total = res.data.data.total;
                    this.pages = res.data.data.pages;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        format: function(parent, key) {
            let val = lodash.get(parent, key);
            if (val && val.length) {
                return val;
            } else {
                return [];
            }
        },
        showBanner: function(val) {
            return val ? showMinibanner(val) : this.defaultBanner;
        },
    },
    filters: {
        dateFormat: function(val) {
            return dateFormat(new Date(val));
        },
        showAvatar: function(val) {
            return showAvatar(val);
        },
        authorLink: function(val) {
            return authorLink(val);
        },
        postLink: function(val) {
            return "./#/view/" + val;
        },
        isHighlight: function(val) {
            return val ? `color:${val};font-weight:600;` : "";
        },
        showMark: function(val) {
            const mark_map = {
                newbie: "新手易用",
                advanced: "进阶推荐",
                recommended: "编辑精选",
                geek: "骨灰必备",
            };
            return mark_map[val];
        },
    },
    mounted: function() {
        let params = new URLSearchParams(location.search);
        this.subtype = params.get("fb_name");
        this.changePage(1);
    },
};
</script>

<style lang="less">
@import "../assets/css/index.less";
</style>
